.home {
    display: flex;
    justify-content: center;
    height: 90vh;
    align-items: 'left';
    flex-direction: column;
    /* background-image: url('../Images/banner1.jpg'); */
    /* object-fit: cover;
    background-repeat: no-repeat; */
    background-position: center;
}

.headerContainer {
    color: #fff;
    width: auto;
    margin-left: 30px;
}

.headerContainer h1 {
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
    font-weight: bold;
}

.headerContainer p {
    font-weight: lighter;
    font-size: 1.2rem;
    margin-top: 15px;
    padding: 0;
}

.headerContainer button {
    margin-top: 1rem;
    cursor: pointer;
    color: #fff;
    font-size: 1.2rem;
    height: 3rem;
    width: 10rem;
    border-radius: 8px;
    background-color: #f57c00;
    text-decoration: none;
    font-weight: bold;
    border: none;
}

.headerContainer button:hover {
    transition: 0.3s ease-in;
    background-color: #b26e2a;

    cursor: pointer;
}

/* mobile view  */
@media only screen and (max-width: 600px) {
    .home {
        justify-content: center;
        align-items: center;
    }

    .headerContainer {
        margin-left: 0;
        border-radius: 10px;
        width: 80%;
        
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: #000;
    }

    .headerContainer h1 {
        font-size: 1.5rem !important;
        margin-top: 16px;
    }

    .headerContainer p {
        font-size: 0.8rem;
    }

    .headerContainer button {
        font-size: 0.8rem;
        height: 1.5rem;
        width: 6rem;
        margin: 1rem 0;
    }

}