.nav-menu{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-menu li {
    margin: 0 20px;
}
.nav-menu li a {
    text-decoration: none;
    color: #fff;
}


/* handle drawer css  */
.mobile-menu {
    list-style: none;
    
}
.mobile-menu li {
    margin-top: 1rem;
}
.mobile-menu li a {
    text-decoration: none;
    color: #f57c00;
    font-size: 1rem;
}
nav a.active {
    color: #27190c;
    font-weight: bold;
    font-size: 1.2rem;
}

